import * as fromBrowseBookResourceAction from './actions';
import {
  initialState,
  BrowseBookInitialState,
  resourceAdapter,
  nodeAdapter
} from './state';

export function browseBookReducer(
  state = initialState,
  actions: fromBrowseBookResourceAction.BrowseBookActions
): BrowseBookInitialState {
  switch (actions.type) {
    case fromBrowseBookResourceAction.BrowseBookActionTypes.SET_RESOURCE: {
      return { ...state, selectedResourceId: actions.payload };
    }

    case fromBrowseBookResourceAction.BrowseBookActionTypes.UPDATE_RESOURCE: {
      return {
        ...state,
        resource: resourceAdapter.updateOne(actions.payload, state.resource)
      };
    }

    case fromBrowseBookResourceAction.BrowseBookActionTypes
      .GET_RESOURCES_SUCCESS: {
      return {
        ...state,
        resource: resourceAdapter.addMany(actions.payload, {
          ...state.resource,
          resources: [...state.resource.resources, ...actions.payload]
        })
      };
    }

    case fromBrowseBookResourceAction.BrowseBookActionTypes.SET_NODE: {
      return {
        ...state,
        selectedNode: actions.payload
      };
    }

    case fromBrowseBookResourceAction.BrowseBookActionTypes.GET_NODES_SUCCESS: {
      return {
        ...state,
        node: nodeAdapter.addMany(actions.payload, {
          ...state.node,
          nodes: [...state.node.nodes, ...actions.payload]
        })
      };
    }
    case fromBrowseBookResourceAction.BrowseBookActionTypes.GET_NODES_FAILED: {
      return {
        ...state,
        error: actions.payload
      };
    }

    case fromBrowseBookResourceAction.BrowseBookActionTypes.IS_NODE_EMPTY: {
      return {
        ...state,
        isNodeEmpty: actions.payload
      };
    }

    case fromBrowseBookResourceAction.BrowseBookActionTypes.IS_RESOURCE_EMPTY: {
      return {
        ...state,
        isResourceEmpty: actions.payload
      };
    }
    
    case fromBrowseBookResourceAction.BrowseBookActionTypes.GET_CONFIG_SUCCESS: {
      return {
        ...state,
        config: actions.payload
      }
    }

    default: {
      return state;
    }
  }
}
