import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { LessonService } from './service';
import * as lessonActions from './actions';
import { MyLessonInterface, MyLessonConfigInterface } from './model';

@Injectable()
export class LessonsEffects {
  constructor(
    private actions$: Actions,
    private lessonService: LessonService
  ) {}

  @Effect()
  getLessons$ = this.actions$.pipe(
    ofType<lessonActions.GetLessons>(
      lessonActions.MyLessonsActionTypes.GET_LESSONS
    ),
    switchMap((actions: lessonActions.GetLessons) =>
      this.lessonService.getAPILessons().pipe(
        switchMap((lesson: MyLessonInterface[]) => [
          new lessonActions.GetLessonsSuccess(lesson),
          new lessonActions.ResourceLoaded(true)
        ]),
        catchError(err => of(new lessonActions.GetLessonsFailed(err.message)))
      )
    )
  );

  @Effect()
  getConfig$ = this.actions$.pipe(
    ofType<lessonActions.GetConfig>(
      lessonActions.MyLessonsActionTypes.GET_CONFIG
    ),
    switchMap((actions: lessonActions.GetConfig) =>
      this.lessonService.getConfig(actions.payload).pipe(
        switchMap((configData: MyLessonConfigInterface) => {
          const config = {};
          config[actions.payload] = configData;
          return [new lessonActions.GetConfigSuccess(config)];
        }),
        catchError(err => of(new lessonActions.GetConfigFailed(err.message)))
      )
    )
  );

  // @Effect()
  // updateLesson$ = this.actions$.pipe(
  //     ofType<lessonActions.UpdateLessonApi>(
  //         lessonActions.LessonsActionType.UPDATE_LESSON_API
  //     ),
  //     mergeMap((actions: lessonActions.UpdateLessonApi) =>
  //         this.lessonService.updateAPILesson(actions.payload).pipe(
  //             map((lesson: LessonInterface) => new lessonActions.UpdateLessonSuccess('Lesson Updated Successfully!')),
  //             catchError(err => of(new lessonActions.UpdateLessonFailed(err.message))),
  //             tap(action => console.log(action))
  //         )
  //     )
  // );
}
