import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { from } from 'rxjs';
import { sharedModuleEventHandlerType } from './shared-module-event-handler.types';

@Injectable()

export class SharedModuleEventHandlerService {
  public listeners;
  public eventsSubject;
  public events;

  constructor() {
    this.listeners = {};
    this.eventsSubject = new Subject();

    this.events = from(this.eventsSubject);

    this.events.subscribe(({ name, args }) => {
      if (this.listeners[name]) {
        for (const listener of this.listeners[name]) {
          listener(...args);
        }
      }
    });
  }

  on(name: sharedModuleEventHandlerType, listener) {
    if (!this.listeners[name]) {
      this.listeners[name] = [];
    }

    this.listeners[name].push(listener);
  }

  off(name: sharedModuleEventHandlerType) {
    this.listeners[name] = null;
  }

  broadcast(name: sharedModuleEventHandlerType, ...args) {
    this.eventsSubject.next({
      name,
      args
    });
  }
}