import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { MyLessonInterface, MyLessonConfigInterface } from './model';

@Injectable()
export class LessonService {
  baseUrl: string;

  constructor(private http: HttpClient) {
    this.baseUrl = 'dummy-api';
  }

  getAPILessons() {
    return this.http
      .get(`${this.baseUrl}/mylessons`)
      .pipe(catchError((error: any) => throwError(error.message)));
  }

  getVideoPlayerData() {
    return this.http
      .get(`${this.baseUrl}/video-player`)
      .pipe(catchError((error: any) => throwError(error.message)));
  }

  updateAPILesson(lesson: MyLessonInterface) {
    return this.http
      .put(`${this.baseUrl}/lessons/${lesson.id}`, lesson)
      .pipe(catchError((error: any) => throwError(error.message)));
  }

  getConfig(type: string){
    return this.http
      .get<MyLessonConfigInterface>(`${this.baseUrl}/mylessons/config/${type}`)
      .pipe(catchError((error: any) => throwError(error.message)));
  }
}
