import { createFeatureSelector, createSelector } from '@ngrx/store';
import {
  tqAdapter,
  resourceAdapter,
  subResourceAdapter,
  TqInitialState,
  RevisePlayerState,
  ResourceInitialState,
  SubResourceInitialState
} from './state';
import { states } from './../root-store.state';
import {
  TqInterface,
  RevisePlayerResourceInterface,
  RevisePlayerSubResourceInterface
} from './model';

const getRevisePlayerFeatureState = createFeatureSelector<RevisePlayerState>(
  states.revisePlayer
);

export const tqState = createSelector(
  getRevisePlayerFeatureState,
  revisePlayer => revisePlayer.tq
);

export const resourceState = createSelector(
  getRevisePlayerFeatureState,
  revisePlayer => revisePlayer.resource
);

export const subResourceState = createSelector(
  getRevisePlayerFeatureState,
  revisePlayer => revisePlayer.subResource
);

export const { selectAll: selectAllTqs } = tqAdapter.getSelectors(tqState);

export const { selectAll: selectAllResource } = resourceAdapter.getSelectors(
  resourceState
);

export const {
  selectAll: selectAllSubResource
} = subResourceAdapter.getSelectors(subResourceState);

export const isTqsEmpty = createSelector(
  tqState,
  selectAllTqs,
  (state: TqInitialState, tqs: TqInterface[]) => {
    if (tqs.length === 0) {
      return true;
    } else {
      return false;
    }
  }
);

export const getResourceByTq = () =>
  createSelector(
    resourceState,
    selectAllResource,
    (
      state: ResourceInitialState,
      resources: RevisePlayerResourceInterface[],
      props: { tqId: string }
    ) => {
      return resources.filter(resource => resource.tqId === props.tqId);
    }
  );

export const getResourceByResourceId = () =>
  createSelector(
    resourceState,
    selectAllResource,
    (
      state: ResourceInitialState,
      resources: RevisePlayerResourceInterface[],
      props: { resourceId: string }
    ) => {
      return resources.filter(
        resource => resource.resourceId === props.resourceId
      )[0];
    }
  );

export const isSubResourcePresent = () =>
  createSelector(
    subResourceState,
    selectAllSubResource,
    (
      state: SubResourceInitialState,
      subResources: RevisePlayerSubResourceInterface[],
      props
    ) => {
      const subResource = subResources.filter(
        subResource => subResource.subResourceId === props.subResource.id
      );
      if (subResource.length === 0) {
        return null;
      } else {
        return subResource[0];
      }
    }
  );

  export const getSubResourceByKeyValue = () => createSelector(
    subResourceState,
    selectAllSubResource,
    (
      state: SubResourceInitialState,
      subResources: RevisePlayerSubResourceInterface[],
      props: {key: string, value: string}
    ) => {
      return subResources.filter(resource => 
        resource[props.key]===props.value
      )
    }
  )

export const getTotalResourceTypeLength = () => createSelector(
  resourceState,
  selectAllResource,
  (
    state: ResourceInitialState,
    resources: RevisePlayerResourceInterface[],
    props: { resourceType: Array<string> }
  ) => {
    const resourceTypeArray = [];
    if(resources.length===0){
      return resourceTypeArray;
    }else{
      props.resourceType.map((resourceType) => {
        let count = 0;
        const filteredResources = resources.filter(resource => resource.resourceType === resourceType);
        filteredResources.map(resource => count += resource.data.length);
        resourceTypeArray.push({
          type: resourceType, value: count
        })
      });
      return resourceTypeArray;
    }
  }
)
