import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import {
  TqInterface,
  RevisePlayerResourceInterface,
  RevisePlayerSubResourceInterface
} from './model';

export interface TqInitialState extends EntityState<TqInterface> {}

export const defaultTq: TqInitialState = {
  ids: [],
  entities: {}
};

export interface ResourceInitialState
  extends EntityState<RevisePlayerResourceInterface> {}

export const defaultResource: ResourceInitialState = {
  ids: [],
  entities: {}
};

export interface SubResourceInitialState
  extends EntityState<RevisePlayerSubResourceInterface> {}

export const defaultSubResource: SubResourceInitialState = {
  ids: [],
  entities: {}
};

export const tqAdapter: EntityAdapter<TqInterface> = createEntityAdapter<
  TqInterface
>({
  selectId: (tq: TqInterface) => tq.tqId
});

export const resourceAdapter: EntityAdapter<RevisePlayerResourceInterface> = createEntityAdapter<
  RevisePlayerResourceInterface
>({
  selectId: (resource: RevisePlayerResourceInterface) => resource.resourceId
});

export const subResourceAdapter: EntityAdapter<RevisePlayerSubResourceInterface> = createEntityAdapter<
  RevisePlayerSubResourceInterface
>({
  selectId: (subResource: RevisePlayerSubResourceInterface) =>
    subResource.subResourceId
});

const tqInitialState = tqAdapter.getInitialState(defaultTq);
const resourceInitialState = resourceAdapter.getInitialState(defaultResource);
const subResourceInitialState = subResourceAdapter.getInitialState(
  defaultSubResource
);

export interface RevisePlayerState {
  tq: TqInitialState;
  resource: ResourceInitialState;
  subResource: SubResourceInitialState;
  isTqsEmpty: boolean;
  error: string;
  success: string;
}

export const initialState: RevisePlayerState = {
  tq: tqInitialState,
  resource: resourceInitialState,
  subResource: subResourceInitialState,
  isTqsEmpty: true,
  error: '',
  success: ''
};
