import { createFeatureSelector, createSelector } from '@ngrx/store';
import { MyLessonstate, myLessonAdapter } from './state';
import { states } from './../root-store.state';

const getMyLessonFeatureState = createFeatureSelector<MyLessonstate>(
  states.myLesson
);

export const getMyLessonInitialFeatureState = createSelector(
  getMyLessonFeatureState,
  myLesson => myLesson.myLesson.lessons
);

export const { selectAll: getLessons } = myLessonAdapter.getSelectors(getMyLessonInitialFeatureState);

export const getCurrentLessonId = createSelector(
  getMyLessonFeatureState,
  (state: MyLessonstate) => state.myLesson.selectedLessonId
);

export const getCurrentLesson = createSelector(
  getMyLessonFeatureState,
  getCurrentLessonId,
  (state: MyLessonstate) => {
    return state.myLesson.lessons.entities[state.myLesson.selectedLessonId];
  }
);

export const isResourceLoaded = createSelector(
  getMyLessonFeatureState,
  (state: MyLessonstate) => {
    return state.myLesson.resourceLoaded;
  }
);

export const getMyLessonConfigByType = () =>
  createSelector(
    getMyLessonFeatureState,
    (state: MyLessonstate, props: {lessonType: string }) => state.myLesson.config[props.lessonType]
  );