import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { EffectsModule } from '@ngrx/effects';
import { StoreModule } from '@ngrx/store';
import { BrowseBookService } from './service';
import { BrowseBookEffects } from './effects';
import { states } from './../root-store.state';
import { reducers, metaReducers } from './metareducers';

@NgModule({
  imports: [
    CommonModule,
    StoreModule.forFeature(states.browseBook, reducers, { metaReducers }),
    EffectsModule.forFeature([BrowseBookEffects])
  ],
  providers: [BrowseBookService, BrowseBookEffects]
})
export class BrowseBookStoreModule {}
