// TQ
export enum TqState {
  LOCK = 0,
  UNLOCK = 1,
  SUCCESS_COMPLETED = 2,
  FAILED_COMPLETED = 3
}

export interface TqInterface {
  tqId: string;
  label: string;
  TpRef: string;
  tqState: TqState;
  totalStar?: string;
  totalStarAchieved?: string;
}
// TQ

// RESOURCE
export enum ResourceState {
  LOCK = 0,
  UNLOCK = 1,
  VISITED = 2,
  COMPLETED = 3
}

export interface RevisePlayerResourceInterface {
  tqId: string;
  resourceId: string;
  resourceType: string;
  data: Array<any>;
  resourceState?: ResourceState;
}
// RESOURCE

// SUB-RESOURCE
export enum SubResourceState {
  LOCK = 0,
  UNLOCK = 1,
  VISITED = 2,
  COMPLETED = 3
}

export interface RevisePlayerSubResourceInterface {
  tqId: string;
  subResourceId: string;
  resourceType: string;
  subResourceState?: SubResourceState;
  data: any;
}
// SUB-RESOURCE

export interface RevisePlayerSubResourceCommonInterface{
  id: string,
  data: string;
}

export enum RevisePLayerResourceType {
  VIDEO = 'video',
  QUIZ = 'quiz',
  SUMMARY = 'summary'
}

export interface RevisePlayerVideo extends RevisePlayerSubResourceCommonInterface {
}

export interface RevisePlayerVideos {
  data: RevisePlayerVideo [];
  resource_type: RevisePLayerResourceType.VIDEO;
}

export interface RevisePlayerQuizQuestion extends RevisePlayerSubResourceCommonInterface {
  order: number;
  type: string;
}

export interface RevisePlayerQuiz {
  data: RevisePlayerQuizQuestion [];
  resource_type: RevisePLayerResourceType.QUIZ;
}

export interface RevisePlayerSummary extends RevisePlayerSubResourceCommonInterface {
  title: string;
  tqId: string;
  tag: string;
  closeBtn: boolean;
}

export interface RevisePlayerSummarys {
  data: RevisePlayerSummary [];
  resource_type: RevisePLayerResourceType.SUMMARY;
}

export interface RevisePLayerInterface {
  label: string;
  status: string;
  tqId: string;
  TpRef: string;
  data: [RevisePlayerVideos | RevisePlayerQuiz | RevisePlayerSummarys];
}
