import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { RootStoreModule } from './core/root-store/root-store.module';
import { AppComponent } from './app.component';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingService } from './app.routing.service';
import { BrowseBookStoreModule, MyLessonStoreModule } from '@root-store';
import { SharedModuleEventHandlerModule } from '@shared-event-handler';
import { HomeComponent } from './home/home.component';
import { HtmlScreensModule } from './html-screens/html-screens.module';
import { ApiInterceptor, TokenInterceptor } from './core/http.interceptor';
import { AuthenticationService } from './core/authentication/authentication.service';
import { TimeUtilityService } from '@utility/time/time-utility.service';

export function checkToken(authenticationService: AuthenticationService) {
  return () => authenticationService.checkToken();
}

@NgModule({
  declarations: [AppComponent, HomeComponent],
  imports: [
    BrowserModule,
    HttpClientModule,
    AppRoutingModule,
    RootStoreModule,
    BrowserAnimationsModule,
    BrowseBookStoreModule,
    MyLessonStoreModule,
    SharedModuleEventHandlerModule.forRoot(),
    HtmlScreensModule
  ],
  providers: [
    AppRoutingService,
    AuthenticationService,
    TimeUtilityService,
    { provide: APP_INITIALIZER, useFactory: checkToken, deps: [AuthenticationService], multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ApiInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: TokenInterceptor, multi: true },
  ],
    bootstrap: [AppComponent]
})
export class AppModule {}
