import { ActionReducer, MetaReducer, ActionReducerMap } from '@ngrx/store';
import * as fromBrowseBook from './state';
import { browseBookReducer } from './reducer';
import { localStorageSync } from 'ngrx-store-localstorage';
import { states } from './../root-store.state';
import { Action } from '@ngrx/store';

export const reducers: ActionReducerMap<fromBrowseBook.BrowseBookState> = {
  browseBook: browseBookReducer
};

export function localStorageSyncReducer(
  reducer: ActionReducer<any>
): ActionReducer<any> {
  return localStorageSync({
    keys: [states.browseBook],
    rehydrate: true
  })(reducer);
}

export const metaReducers: Array<MetaReducer<
  fromBrowseBook.BrowseBookState,
  Action
>> = [localStorageSyncReducer];
