import { Action } from '@ngrx/store';
import { Update } from '@ngrx/entity';
import { NodeInterface, ResourceInterface, BrowseBookConfigInterface, BrowseBookConfigTypeInterface } from './model';

export enum BrowseBookActionTypes {
  SET_RESOURCE = '[BrowseBook] SET_RESOURCE',
  GET_RESOURCES = '[BrowseBook] GET_RESOURCES',
  GET_RESOURCES_SUCCESS = '[BrowseBook] GET_RESOURCES_SUCCESS',
  GET_RESOURCES_FAILED = '[BrowseBook] GET_RESOURCES_FAILED',
  UPDATE_RESOURCE = '[BrowseBook] UPDATE_RESOURCE',

  PREV_NODE = '[BrowseBook] PREV_NODE',
  GET_NODES = '[BrowseBook] GET_NODES',
  SET_NODE = '[BrowseBook] SET_NODE',
  GET_NODES_SUCCESS = '[BrowseBook] GET_NODES_SUCCESS',
  GET_NODES_FAILED = '[BrowseBook] GET_NODES_FAILED',

  IS_NODE_EMPTY = '[BrowseBook] IS_NODE_EMPTY',
  IS_RESOURCE_EMPTY = '[BrowseBook] IS_RESOURCE_EMPTY',

  GET_CONFIG = '[BrowseBook] GET_CONFIG',
  GET_CONFIG_SUCCESS = '[BrowseBook] GET_CONFIG_SUCCESS',
  GET_CONFIG_FAILED = '[BrowseBook] GET_CONFIG_FAILED'
}

export class SetResource implements Action {
  readonly type = BrowseBookActionTypes.SET_RESOURCE;

  constructor(public payload: string) {}
}
export class GetResources implements Action {
  readonly type = BrowseBookActionTypes.GET_RESOURCES;
}
export class GetResourcesSuccess implements Action {
  readonly type = BrowseBookActionTypes.GET_RESOURCES_SUCCESS;
  constructor(public payload: ResourceInterface[]) {}
}
export class GetResourcesFailed implements Action {
  readonly type = BrowseBookActionTypes.GET_RESOURCES_FAILED;
  constructor(public payload: string) {}
}
export class UpdateResource implements Action {
  readonly type = BrowseBookActionTypes.UPDATE_RESOURCE;
  constructor(public payload: Update<ResourceInterface>) {}
}

export class PrevNode implements Action {
  readonly type = BrowseBookActionTypes.PREV_NODE;
}
export class GetNodes implements Action {
  readonly type = BrowseBookActionTypes.GET_NODES;
}
export class SetNode implements Action {
  readonly type = BrowseBookActionTypes.SET_NODE;

  constructor(public payload: string) {}
}
export class GetNodesSuccess implements Action {
  readonly type = BrowseBookActionTypes.GET_NODES_SUCCESS;

  constructor(public payload: NodeInterface[]) {}
}
export class GetNodesFailed implements Action {
  readonly type = BrowseBookActionTypes.GET_NODES_FAILED;

  constructor(public payload: string) {}
}

export class IsNodeEmpty implements Action {
  readonly type = BrowseBookActionTypes.IS_NODE_EMPTY;

  constructor(public payload: boolean) {}
}
export class IsResourceEmpty implements Action {
  readonly type = BrowseBookActionTypes.IS_RESOURCE_EMPTY;

  constructor(public payload: boolean) {}
}

export class GetConfig implements Action {
  readonly type = BrowseBookActionTypes.GET_CONFIG;

  constructor(public payload: string) {}
}
export class GetConfigSuccess implements Action {
  readonly type = BrowseBookActionTypes.GET_CONFIG_SUCCESS;

  constructor(public payload: BrowseBookConfigTypeInterface) {}
}
export class GetConfigFailed implements Action {
  readonly type = BrowseBookActionTypes.GET_CONFIG_FAILED;

  constructor(public payload: string) {}
}

export type BrowseBookActions =
  | GetResources
  | SetResource
  | GetResourcesSuccess
  | GetResourcesFailed
  | UpdateResource
  | PrevNode
  | GetNodes
  | SetNode
  | GetNodesSuccess
  | GetNodesFailed
  | IsNodeEmpty
  | IsResourceEmpty
  | GetConfig
  | GetConfigSuccess
  | GetConfigFailed;
