import * as fromMyLessonAction from './actions';
import { MyLessonInitialState, myLessonAdapter, initialState } from './state';

export function lessonsReducer(
  state = initialState,
  actions: fromMyLessonAction.MyLessonsActions
): MyLessonInitialState {
  switch (actions.type) {
    case fromMyLessonAction.MyLessonsActionTypes.SET_RESOURCE: {
      return { ...state, selectedLessonId: actions.payload };
    }
    case fromMyLessonAction.MyLessonsActionTypes.GET_LESSON: {
      return { ...state, selectedLessonId: actions.payload };
    }
    case fromMyLessonAction.MyLessonsActionTypes.GET_LESSONS_SUCCESS: {
      let msgText = '';

      if (actions.payload.length < 1) {
        msgText = 'No data found';
      } else {
        msgText = 'Loading data';
      }

      return {
        ...state,
        lessons: myLessonAdapter.addMany(actions.payload, {
          ...state.lessons,
          lesson: [...state.lessons.lesson, ...actions.payload]
        })
      }
    }
    case fromMyLessonAction.MyLessonsActionTypes.GET_LESSONS_FAILED: {
      return { ...state, error: actions.payload };
    }

    case fromMyLessonAction.MyLessonsActionTypes.RESOURCE_LOADED: {
      return { ...state, resourceLoaded: actions.payload };
    }
    case fromMyLessonAction.MyLessonsActionTypes.UPDATE_RESOURCE: {
      return {
        ...state,
        lessons: myLessonAdapter.updateOne(actions.payload, state.lessons)
      };
    }

    case fromMyLessonAction.MyLessonsActionTypes.GET_CONFIG_SUCCESS: {
      return {
        ...state,
        config: actions.payload
      }
    }

    default: {
      return state;
    }
  }
}
