import { createFeatureSelector, createSelector } from '@ngrx/store';
import {
  ResourceState,
  resourceAdapter,
  nodeAdapter,
  NodeState,
  BrowseBookState
} from './state';
import { states } from './../root-store.state';

const getBrowseBookFeatureState = createFeatureSelector<BrowseBookState>(
  states.browseBook
);

export const resourcesState = createSelector(
  getBrowseBookFeatureState,
  browsebook => browsebook.browseBook.resource
);

export const nodeState = createSelector(
  getBrowseBookFeatureState,
  node => node.browseBook.node
);

export const { selectAll: selectAllResources } = resourceAdapter.getSelectors(
  resourcesState
);

export const { selectAll: selectAllNodes } = nodeAdapter.getSelectors(
  nodeState
);

export const getCurrentResourceId = createSelector(
  getBrowseBookFeatureState,
  (state: BrowseBookState) => state.browseBook.selectedResourceId
);

export const getCurrentResource = createSelector(
  resourcesState,
  getCurrentResourceId,
  (state: ResourceState, selectedId: string) => {
    return state.entities[selectedId];
  }
);

export const getSelectedNodeId = createSelector(
  getBrowseBookFeatureState,
  (state: BrowseBookState) => state.browseBook.selectedNode
);

export const getNodeList = createSelector(nodeState, state => state.entities);
export const isNodePresent = createSelector(
  nodeState,
  getSelectedNodeId,
  (state: NodeState, selectedNode: string) => {
    return Object.values(state.entities).filter(
      val => val.parentNodeId === selectedNode
    );
  }
);

export const isResourcePresent = createSelector(
  resourcesState,
  getSelectedNodeId,
  (state: ResourceState, selectedResource: string) => {
    return Object.values(state.entities).filter(
      val => val.nodeId === selectedResource
    );
  }
);

export const getSelectedNodeList = createSelector(
  nodeState,
  getSelectedNodeId,
  (state: NodeState, selectedNode: string) => {
    return state.nodes.filter(node => node.parentNodeId === selectedNode);
  }
);

export const isNodeEmpty = createSelector(
  getBrowseBookFeatureState,
  (state: BrowseBookState) => state.browseBook.isNodeEmpty
);

export const isResourceEmpty = createSelector(
  getBrowseBookFeatureState,
  (state: BrowseBookState) => state.browseBook.isResourceEmpty
);

export const getBrowseBookConfigByType = () =>
  createSelector(
    getBrowseBookFeatureState,
    (state: BrowseBookState, props: {lessonType: string }) => state.browseBook.config[props.lessonType]
  );
