import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent
  },
  {
    path: 'browseBook',
    loadChildren: () =>
      import('./browse-book/browse.book.module').then(
        m => m.BrowseBookModule
      )
  },
  {
    path: 'myLesson',
    loadChildren: () =>
      import('./my-lesson/my-lesson.module').then(
        m => m.MyLessonModule
      )
  },
  {
    path: 'lesson',
    loadChildren: () =>
      import('./lesson-player/lesson-player.module').then(
        m => m.LessonPlayerModule
      )
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {}
