import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { EffectsModule } from '@ngrx/effects';
import { LessonService } from './service';
import { LessonsEffects } from './effects';
import { states } from './../root-store.state';
import { StoreModule } from '@ngrx/store';
import { reducers, metaReducers } from './metareducers';

@NgModule({
  imports: [
    CommonModule,
    StoreModule.forFeature(states.myLesson, reducers, { metaReducers }),
    EffectsModule.forFeature([LessonsEffects])
  ],
  providers: [LessonService, LessonsEffects]
})
export class MyLessonStoreModule {}
