import { Injectable } from '@angular/core';
import {
  HttpInterceptor,
  HttpRequest,
  HttpHandler,
  HttpEvent
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { AuthenticationService } from './authentication/authentication.service';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  constructor(private auth: AuthenticationService) {}
  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    req = req.clone({
      setHeaders: {
        Authorization: `Bearer ${this.auth.getToken()}`
      }
    });
    return next.handle(req);
  }
}

export class ApiInterceptor implements HttpInterceptor {
  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    let apiReq;
    const hostName = document.location.hostname;
    let isApi = req.url;
    let isApiArray = isApi.split('/');
    let domains = [
      'ui-static-files.s3-website.ap-south-1.amazonaws.com',
      'tce-afs-web.firebaseapp.com'
    ];

    if (
      domains.includes(hostName) && (isApiArray[0] === 'tce' || isApiArray[0] === 'student')
    ) {
      apiReq = req.clone({
        url: `http://13.235.241.238:8080/${req.url}`
      });
    } else if (domains.includes(hostName) && isApiArray[0] === 'dummy-api') {
      apiReq = req.clone({ url: `https://classedgeapps.firebaseapp.com/api/${req.url}` });
    } else {
      apiReq = req.clone({ url: `${req.url}` });
    }

    return next.handle(apiReq);
  }
}
