import { NgModule, ModuleWithProviders } from '@angular/core';
import { SharedModuleEventHandlerService } from './shared-module-event-handler.service';

@NgModule({})

export class SharedModuleEventHandlerModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModuleEventHandlerModule,
      providers: [ SharedModuleEventHandlerService ]
    }
  }
}
