import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { MyLessonInterface, MyLessonConfigTypeInterface } from './model';

export interface LessonState extends EntityState<MyLessonInterface> {
  lesson: MyLessonInterface [];
}

export const defaultLesson: LessonState = {
  ids: [],
  entities: {},
  lesson: []
};

export const myLessonAdapter: EntityAdapter<MyLessonInterface> = createEntityAdapter<
  MyLessonInterface
>();

export const myLessonInitialState = myLessonAdapter.getInitialState(defaultLesson);

export interface MyLessonInitialState{
  selectedLessonId: string | null;
  resourceLoaded: boolean;
  lessons: LessonState;
  config?: MyLessonConfigTypeInterface;
  error: string;
  success: string;
}

export const initialState: MyLessonInitialState = {
  selectedLessonId: null,
  resourceLoaded: false,
  lessons: myLessonInitialState,
  config: {},
  error: '',
  success: ''
};

export interface MyLessonstate {
  myLesson: MyLessonInitialState;
}
