import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { NodeInterface, ResourceInterface, BrowseBookConfigInterface } from './model';

@Injectable()
export class BrowseBookService {
  nodeUrl: string = 'dummy-api/browse-book/nodes';
  resourceUrl: string = 'dummy-api/browse-book/resources';
  configUrl: string = 'dummy-api/browse-book/config';

  constructor(private http: HttpClient) {}

  getNode(nodeType: string) {
    return this.http
      .get<NodeInterface[]>(`${this.nodeUrl}/${nodeType}`)
      .pipe(catchError((error: any) => throwError(error.message)));
  }

  getResource(resourceType: string) {
    return this.http
      .get<ResourceInterface[]>(`${this.resourceUrl}/${resourceType}`)
      .pipe(catchError((error: any) => throwError(error.message)));
  }

  getConfig(type: string){
    return this.http
      .get<BrowseBookConfigInterface>(`${this.configUrl}/${type}`)
      .pipe(catchError((error: any) => throwError(error.message)));
  }
}
