import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, switchMap, withLatestFrom } from 'rxjs/operators';
import { of, Observable } from 'rxjs';
import { BrowseBookService } from './service';
import * as browseBookActions from './actions';
import { NodeInterface, ResourceInterface, BrowseBookConfigInterface } from './model';
import { State } from './../root-store.state';
import { Store } from '@ngrx/store';

@Injectable()
export class BrowseBookEffects {
  constructor(
    private actions$: Actions,
    private browseBookService: BrowseBookService,
    private store: Store<State>
  ) {}

  @Effect()
  getResources$ = this.actions$.pipe(
    ofType<browseBookActions.GetResources>(
      browseBookActions.BrowseBookActionTypes.GET_RESOURCES
    ),
    withLatestFrom(this.store),
    switchMap(([action, store]) => {
      return this.browseBookService
        .getResource(store.browseBook.browseBook.selectedNode)
        .pipe(
          switchMap((resource: ResourceInterface[]) => {
            if (resource.length != 0) {
              return [new browseBookActions.GetResourcesSuccess(resource)];
            } else {
              return [new browseBookActions.IsResourceEmpty(true)];
            }
          }),
          catchError(err =>
            of(new browseBookActions.GetResourcesFailed(err.message))
          )
        );
    })
  );

  @Effect()
  getNode$ = this.actions$.pipe(
    ofType<browseBookActions.GetNodes>(
      browseBookActions.BrowseBookActionTypes.GET_NODES
    ),
    withLatestFrom(this.store),
    switchMap(([action, store]) => {
      return this.browseBookService
        .getNode(store.browseBook.browseBook.selectedNode)
        .pipe(
          switchMap((resource: NodeInterface[]) => {
            if (resource.length != 0) {
              return [new browseBookActions.GetNodesSuccess(resource)];
            } else {
              return [new browseBookActions.IsNodeEmpty(true)];
            }
          }),
          catchError(err =>
            of(new browseBookActions.GetNodesFailed(err.message))
          )
        );
    })
  );

  @Effect()
  $goBack = this.actions$.pipe(
    ofType<browseBookActions.PrevNode>(
      browseBookActions.BrowseBookActionTypes.PREV_NODE
    ),
    withLatestFrom(this.store),
    switchMap(([action, store]) => {
      const navigateToNode = store.browseBook.browseBook.node.entities[
        store.browseBook.browseBook.selectedNode
      ] as NodeInterface;
      if (navigateToNode) {
        return of(new browseBookActions.SetNode(navigateToNode.parentNodeId));
      } else {
        return new Observable();
      }
    })
  );

  @Effect()
  getConfig$ = this.actions$.pipe(
    ofType<browseBookActions.GetConfig>(
      browseBookActions.BrowseBookActionTypes.GET_CONFIG
    ),
    switchMap((actions: browseBookActions.GetConfig) => 
    this.browseBookService.getConfig(actions.payload).pipe(
      switchMap((configData: BrowseBookConfigInterface) => {
        const config = {};
        config[actions.payload] = configData;
        return [
          new browseBookActions.GetConfigSuccess(config),
        ]
      }),
      catchError(err => of(new browseBookActions.GetConfigFailed(err.message)))
    ))
  );
}
