import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {
  TimeUtilityService,
  DateTypes
} from '@utility/time/time-utility.service';
import { Observable } from 'rxjs';

export interface AuthenticationTokenDetails {
  accessToken: string;
  clientId: string;
  refreshToken: string;
}

@Injectable()
export class AuthenticationService {
  authenticateUrl = 'tce/login';

  constructor(
    private http: HttpClient,
    private timeUtilityService: TimeUtilityService
  ) {}

  authenticate(credentials) {
    return this.http.post<AuthenticationTokenDetails>(
      this.authenticateUrl,
      credentials
    );
  }

  isTokenExpired() {
    const token = JSON.parse(localStorage.getItem('token'));
    const getTimeSpan = this.timeUtilityService.differenceInTime(
      new Date(),
      new Date(token.date)
    );
    if (getTimeSpan.days > 10) {
      return true;
    } else {
      return false;
    }
  }

  isTokenPresent() {
    const token = localStorage.getItem('token');
    return token ? true : false;
  }

  checkToken(): Promise<void> {
    return new Promise((resolve, reject) => {
      if (this.isTokenPresent() && !this.isTokenExpired()) {
        resolve();
      } else {
        this.authenticate({ userName: 'ajay', password: 'ajay' }).subscribe(
          tokenDetails => {
            localStorage.setItem(
              'token',
              JSON.stringify({
                value: tokenDetails.accessToken,
                date: this.timeUtilityService.getCurrentDate()
              })
            );
            resolve();
          },
          error => {
            reject(error);
          }
        );
      }
    });
  }

  getToken() {
    const token = JSON.parse(localStorage.getItem('token'));
    if(token){
      return token.value;
    }else{
      return null;
    }
  }
}
