import { Action } from '@ngrx/store';
import { Update } from '@ngrx/entity';
import {
  RevisePLayerInterface,
  RevisePlayerResourceInterface,
  RevisePlayerSubResourceInterface,
  TqInterface,
  TqState,
  ResourceState,
  SubResourceState,
  RevisePlayerVideo,
  RevisePlayerQuizQuestion,
  RevisePlayerSummary,
  RevisePlayerSubResourceCommonInterface
} from './model';
import { RevisePlayerState } from './state';

export enum RevisePlayerActionTypes {
  GET_TQS = '[RevisePlayer] GET_TQS',
  GET_TQS_SUCCESS = '[RevisePlayer] GET_TQS_SUCCESS',
  GET_TQS_FAILED = '[RevisePlayer] GET_TQS_FAILED',
  GET_TQS_RESOURCE_SUCCESS = '[RevisePlayer] GET_TQS_RESOURCE_SUCCESS',
  GET_SUB_RESOURCE_SUCCESS = '[RevisePlayer] GET_SUB_RESOURCE_SUCCESS',
  GET_SUB_RESOURCE_FAILED = '[RevisePlayer] GET_SUB_RESOURCE_FAILED',
  IS_TQS_EMPTY = "[RevisePlayer] IS_TQS_EMPTY",
  UPDATE_TQ_STATE = "[RevisePlayer] UPDATE_TQ_STATE",
  UPDATE_RESOURCE_STATE = "[RevisePlayer] UPDATE_RESOURCE_STATE",
  UPDATE_SUB_RESOURCE_STATE = "[RevisePlayer] UPDATE_SUB_RESOURCE_STATE",
  GET_SUB_RESOURCE = "[RevisePlayer] GET_SUB_RESOURCE",
  ADD_SUB_RESOURCE_INTERACTION = "[RevisePlayer] ADD_SUB_RESOURCE_INTERACTION",
  RESET_STATE = "[RevisePlayer] RESET_STATE"
}

export class GetTqs implements Action {
  readonly type = RevisePlayerActionTypes.GET_TQS;
}
export class GetTqsSuccess implements Action {
  readonly type = RevisePlayerActionTypes.GET_TQS_SUCCESS;
  constructor(public payload: TqInterface[]) {}
}
export class GetTqsResourceSuccess implements Action {
  readonly type = RevisePlayerActionTypes.GET_TQS_RESOURCE_SUCCESS;
  constructor(public payload: RevisePlayerResourceInterface[]) {}
}
export class GetTqsFailed implements Action {
  readonly type = RevisePlayerActionTypes.GET_TQS_FAILED;
  constructor(public payload: string) {}
}
export class GetSubResourceSuccess implements Action {
  readonly type = RevisePlayerActionTypes.GET_SUB_RESOURCE_SUCCESS;
  constructor(public payload: RevisePlayerSubResourceInterface []) {}
}
export class GetSubResourceFailed implements Action {
  readonly type = RevisePlayerActionTypes.GET_SUB_RESOURCE_FAILED;
  constructor(public payload: string) {}
}

export class IsTqsEmpty implements Action {
  readonly type = RevisePlayerActionTypes.IS_TQS_EMPTY;
  constructor(public payload: boolean) {}
}
export class UpdateTqState implements Action {
  readonly type = RevisePlayerActionTypes.UPDATE_TQ_STATE;
  constructor(public payload: Update<TqInterface>) {}
}
export class UpdateResourceState implements Action {
  readonly type = RevisePlayerActionTypes.UPDATE_RESOURCE_STATE;
  constructor(public payload: Update<RevisePlayerResourceInterface>) {}
}
export class UpdateSubResourceState implements Action {
  readonly type = RevisePlayerActionTypes.UPDATE_SUB_RESOURCE_STATE;
  constructor(public payload: Update<RevisePlayerSubResourceInterface>) {}
}
export class GetSubResource implements Action {
  readonly type = RevisePlayerActionTypes.GET_SUB_RESOURCE;
  constructor(public payload: RevisePlayerSubResourceInterface) {
  }
}
export class ResetState implements Action {
  readonly type = RevisePlayerActionTypes.RESET_STATE;
}
export class AddSubResourceInteraction implements Action {
  readonly type = RevisePlayerActionTypes.ADD_SUB_RESOURCE_INTERACTION;
}

export type RevisePlayerActions =
  | GetTqs
  | GetTqsSuccess
  | GetTqsResourceSuccess
  | GetSubResourceSuccess
  | GetSubResourceFailed
  | GetTqsFailed
  | IsTqsEmpty
  | UpdateTqState
  | UpdateResourceState
  | UpdateSubResourceState
  | GetSubResource
  | ResetState;
