import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { EffectsModule } from '@ngrx/effects';
import { StoreModule } from '@ngrx/store';
import { states } from './../root-store.state';
import { revisePlayerReducer } from './reducer';
import { RevisePlayerEffects } from './effects';
import { RevisePlayerService } from './service';

@NgModule({
  imports: [
    CommonModule,
    StoreModule.forFeature(states.revisePlayer, revisePlayerReducer),
    EffectsModule.forFeature([RevisePlayerEffects])
  ],
  providers: [RevisePlayerEffects, RevisePlayerService]
})
export class RevisePlayerStoreModule {}
