import * as fromRevisePlayerAction from './actions';
import {
  initialState,
  RevisePlayerState,
  tqAdapter,
  resourceAdapter,
  subResourceAdapter
} from './state';

export function revisePlayerReducer(
  state = initialState,
  actions: fromRevisePlayerAction.RevisePlayerActions
): RevisePlayerState {
  switch (actions.type) {
    case fromRevisePlayerAction.RevisePlayerActionTypes.GET_TQS_SUCCESS: {
      return {
        ...state,
        tq: tqAdapter.addMany(actions.payload, state.tq)
      };
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes
      .GET_TQS_RESOURCE_SUCCESS: {
      return {
        ...state,
        resource: resourceAdapter.addMany(actions.payload, state.resource)
      };
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes.GET_TQS_FAILED: {
      return {
        ...state,
        error: actions.payload
      };
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes.IS_TQS_EMPTY: {
      return {
        ...state,
        isTqsEmpty: actions.payload
      };
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes.UPDATE_TQ_STATE: {
      return {
        ...state,
        tq: tqAdapter.updateOne(actions.payload, state.tq)
      };
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes.UPDATE_RESOURCE_STATE: {
      return {
        ...state,
        resource: resourceAdapter.updateOne(actions.payload, state.resource)
      };
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes.UPDATE_SUB_RESOURCE_STATE: {
      return {
        ...state,
        subResource: subResourceAdapter.updateOne(actions.payload, state.subResource)
      };
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes.GET_SUB_RESOURCE_SUCCESS: {
      return {
        ...state,
        subResource: subResourceAdapter.addMany(actions.payload, state.subResource)
      }
    }
    case fromRevisePlayerAction.RevisePlayerActionTypes.RESET_STATE: {
      state = initialState;
      return state;
    }
    default: {
      return state;
    }
  }
}
