import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';
import { NodeInterface, ResourceInterface, BrowseBookConfigInterface, BrowseBookConfigTypeInterface } from './model';

export interface ResourceState extends EntityState<ResourceInterface> {
  resources: ResourceInterface[];
}

export const defaultResource: ResourceState = {
  ids: [],
  entities: {},
  resources: []
};

export interface NodeState extends EntityState<NodeInterface> {
  nodes: NodeInterface[];
}

export const defaultNode: NodeState = {
  ids: [],
  entities: {},
  nodes: []
};

export const resourceAdapter: EntityAdapter<ResourceInterface> = createEntityAdapter<
  ResourceInterface
>({
  selectId: (resource: ResourceInterface) => resource.resourceId
});
export const nodeAdapter: EntityAdapter<NodeInterface> = createEntityAdapter<
  NodeInterface
>({
  selectId: (node: NodeInterface) => node.nodeId
});

const resourceInitialState = resourceAdapter.getInitialState(defaultResource);
const nodeInitialState = nodeAdapter.getInitialState(defaultNode);

export interface BrowseBookInitialState {
  resource: ResourceState;
  node: NodeState;
  selectedResourceId: string | null;
  selectedNode: string;
  isNodeEmpty: boolean;
  isResourceEmpty: boolean;
  config?: BrowseBookConfigTypeInterface;
  error: string;
  success: string;
}

export const initialState: BrowseBookInitialState = {
  resource: resourceInitialState,
  node: nodeInitialState,
  selectedResourceId: null,
  selectedNode: 'root',
  isNodeEmpty: true,
  config: {},
  isResourceEmpty: true,
  error: '',
  success: ''
};

export interface BrowseBookState {
  browseBook: BrowseBookInitialState;
}
