import { Action } from '@ngrx/store';
import { Update } from '@ngrx/entity';
import { MyLessonInterface, MyLessonConfigTypeInterface } from './model';

export enum MyLessonsActionTypes {
  SET_RESOURCE = '[MyLesson]  SET_RESOURCE',
  UPDATE_RESOURCE = '[MyLesson] UPDATE_RESOURCE',
  GET_RESOURCES = '[MyLesson] GET_RESOURCES',
  GET_LESSON = '[MyLesson] GET_LESSON',
  GET_LESSONS = '[MyLesson] GET_LESSONS',
  GET_LESSONS_SUCCESS = '[MyLesson] GET_LESSONS_SUCCESS',
  GET_LESSONS_FAILED = '[MyLesson] GET_LESSONS_FAILED',
  RESOURCE_LOADED = '[MyLesson] RESOURCE_LOADED',
  GET_CONFIG = '[MyLesson] GET_CONFIG',
  GET_CONFIG_SUCCESS = '[MyLesson] GET_CONFIG_SUCCESS',
  GET_CONFIG_FAILED = '[MyLesson] GET_CONFIG_FAILED'
}

export class SetResource implements Action {
  readonly type = MyLessonsActionTypes.SET_RESOURCE;

  constructor(public payload: string) {}
}

export class UpdateResource implements Action {
  readonly type = MyLessonsActionTypes.UPDATE_RESOURCE;
  constructor(public payload: Update<MyLessonInterface>) {}
}
export class GetResources implements Action {
  readonly type = MyLessonsActionTypes.GET_RESOURCES;
}
export class GetLessons implements Action {
  readonly type = MyLessonsActionTypes.GET_LESSONS;
}

export class GetLessonsSuccess implements Action {
  readonly type = MyLessonsActionTypes.GET_LESSONS_SUCCESS;

  constructor(public payload: Array<MyLessonInterface>) {}
}
export class GetLessonsFailed implements Action {
  readonly type = MyLessonsActionTypes.GET_LESSONS_FAILED;
  constructor(public payload: string) {}
}

export class ResourceLoaded implements Action {
  readonly type = MyLessonsActionTypes.RESOURCE_LOADED;

  constructor(public payload: boolean) {}
}

export class GetLesson implements Action {
  readonly type = MyLessonsActionTypes.GET_LESSON;

  constructor(public payload: string) {}
}

export class GetConfig implements Action {
  readonly type = MyLessonsActionTypes.GET_CONFIG;

  constructor(public payload: string) {}
}
export class GetConfigSuccess implements Action {
  readonly type = MyLessonsActionTypes.GET_CONFIG_SUCCESS;

  constructor(public payload: MyLessonConfigTypeInterface) {}
}
export class GetConfigFailed implements Action {
  readonly type = MyLessonsActionTypes.GET_CONFIG_FAILED;

  constructor(public payload: string) {}
}

export type MyLessonsActions =
  | GetLesson
  | SetResource
  | GetLessons
  | GetLessonsSuccess
  | GetLessonsFailed
  | ResourceLoaded
  | GetResources
  | UpdateResource
  | GetConfig
  | GetConfigSuccess
  | GetConfigFailed;
