import { ActionReducer, MetaReducer, ActionReducerMap } from '@ngrx/store';
import * as fromMylesson from './state';
import { lessonsReducer } from './reducer';
import { localStorageSync } from 'ngrx-store-localstorage';
import { states } from './../root-store.state';

export const reducers: ActionReducerMap<fromMylesson.MyLessonstate> = {
  myLesson: lessonsReducer
};

export function localStorageSyncReducer(
  reducer: ActionReducer<any>
): ActionReducer<any> {
  return localStorageSync({
    keys: [states.myLesson],
    rehydrate: true
  })(reducer);
}

export const metaReducers: Array<MetaReducer<any, any>> = [
  localStorageSyncReducer
];
