import { Injectable } from '@angular/core';

export enum DateTypes {
  TIME = 'time'
}

@Injectable()
export class TimeUtilityService {
  date = {
    time: (date: Date) => this.getCurrentDateTime(date)
  };

  constructor() {}

  getCurrentDate(params?: DateTypes) {
    const newDate = new Date();
    if (params) {
      return this.date[params](newDate);
    } else {
      console.log(newDate)
      return newDate;
    }
  }

  getCurrentDateTime(date: Date) {
    return date.getTime();
  }

  /*
    0 ==> Equal
    1 ==> Less
    2 ==> Greater
  */
  twoDateCompareStatus(date1: Date, date2: Date): number {
    if (date1.getTime() === date2.getTime()) {
      return 0;
    }

    if (date1.getTime() < date2.getTime()) {
      return 1;
    } else {
      return 2;
    }
  }

  differenceInTime(startDate: Date, endDate: Date) {
    const difference = Math.abs(startDate.getTime() - endDate.getTime()) / 1000;
    const days = Math.floor((startDate.getTime()-endDate.getTime())/(24*3600*1000));
    const hours = Math.floor(difference / 3600) % 24;
    const minutes = Math.floor(difference / 60) % 60;
    const seconds = Math.floor(difference % 60);
    return { days, hours, minutes, seconds };
  }
}
