import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, switchMap } from 'rxjs/operators';
import { of, Observable } from 'rxjs';
import { RevisePlayerService } from './service';
import * as revisePlayerActions from './actions';
import {
  RevisePLayerInterface,
  TqInterface,
  RevisePlayerResourceInterface,
  TqState,
  ResourceState,
  RevisePLayerResourceType
} from './model';

@Injectable()
export class RevisePlayerEffects {
  resourceMap = {
    assets: RevisePLayerResourceType.VIDEO,
    questions: RevisePLayerResourceType.QUIZ
  }
  constructor(
    private actions$: Actions,
    private revisePlayerService: RevisePlayerService
  ) {}

  @Effect()
  getRevise$ = this.actions$.pipe(
    ofType<revisePlayerActions.GetTqs>(
      revisePlayerActions.RevisePlayerActionTypes.GET_TQS
    ),
    switchMap((actions: revisePlayerActions.GetTqs) =>
      this.revisePlayerService.getRevise().pipe(
        switchMap((reviseData: any) => {
          if (reviseData.length > 0) {
            let tq: TqInterface[] = [];
            let resource: RevisePlayerResourceInterface[] = [];
            
            for (let i = 0; i < reviseData.length; i++) {
              tq.push({
                tqId: reviseData[i].tqId,
                label: reviseData[i].label,
                TpRef: reviseData[i].TpRef,
                tqState: TqState.UNLOCK
              });

              for(let j=0;j<Object.keys(reviseData[i].revise).length;j++) {
                const resourceType = this.resourceMap[Object.keys(reviseData[i].revise)[j]];
                if(reviseData[i].revise[Object.keys(reviseData[i].revise)[j]].length!==0) {
                  resource.push({
                    tqId: reviseData[i].tqId,
                    resourceId: `${reviseData[i].tqId}_${resourceType}`,
                    resourceType: resourceType,
                    data: reviseData[i].revise[Object.keys(reviseData[i].revise)[j]],
                    resourceState: ResourceState.UNLOCK
                  });
                }
              }

              if(reviseData[i].summary.cards.length!==0) {
                resource.push({
                  tqId: reviseData[i].tqId,
                  resourceId: `${reviseData[i].tqId}_${RevisePLayerResourceType.SUMMARY}`,
                  resourceType: RevisePLayerResourceType.SUMMARY,
                  data: reviseData[i].summary.cards,
                  resourceState: ResourceState.UNLOCK
                });
              }

            }

            return [
              new revisePlayerActions.GetTqsSuccess(tq),
              new revisePlayerActions.GetTqsResourceSuccess(resource)
            ];
          } else {
            return [new revisePlayerActions.IsTqsEmpty(true)];
          }
        }),
        catchError(err => of(new revisePlayerActions.GetTqsFailed(err.message)))
      )
    )
  );

  @Effect()
  $getSubResource = this.actions$.pipe(
    ofType<revisePlayerActions.GetSubResource>(
      revisePlayerActions.RevisePlayerActionTypes.GET_SUB_RESOURCE
    ),
    switchMap((actions: revisePlayerActions.GetSubResource) =>
      this.revisePlayerService.getSubResource(actions.payload).pipe(
        switchMap(subResourceData => {
          const subResource = {...actions.payload};
          subResource.data = subResourceData;
          return [
            new revisePlayerActions.GetSubResourceSuccess([subResource])
          ];
        }),
        catchError(err => of(new revisePlayerActions.GetSubResourceFailed(err.message)))
      )
    )
  );
}
